import PropTypes from "prop-types";
import { useDispatch } from "react-redux";
import { addFilter, removeFilter } from "store/filters/filter-actions";
import { ReactComponent as Remove } from "../assets/images/icon-remove.svg";

const Badge = ({
    variant = "basic",
    colorScheme = "light",
    children,
    onClear,
    onClick,
}) => {
    const dispatch = useDispatch();

    function handleFilter(e) {
        const filter = e.currentTarget.innerText;

        if (!!filter) {
            if (variant !== "clearable") {
                dispatch(addFilter(filter));
            } else {
                dispatch(removeFilter(filter));
            }
        }
    }

    return (
        <div
            className={`badge badge--${variant} badge--${colorScheme}`}
            onClick={(e) => handleFilter(e)}
        >
            <span>{children}</span>
            {variant === "clearable" && (
                <div className='badge-remover' onClick={onClear}>
                    <Remove />
                </div>
            )}
        </div>
    );
};

export { Badge };

Badge.propTypes = {
    variant: PropTypes.oneOf(["basic", "clearable", "rounded"]),
    colorScheme: PropTypes.oneOf(["light", "primary", "dark"]),
    children: PropTypes.node.isRequired,
    onClear: PropTypes.func,
    onClick: PropTypes.func,
};
